import {
	mapState,
	mapMutations
} from 'vuex';
import store from "../../../../../store/index.js";
import api from "../../../../../api/api.js";
export default {
	computed: mapState(['hasLogin', 'userInfo', 'token']),
	data() {
		return {
			phone:''
		}
	},
	methods: {
		...mapMutations(['logout']),
		logoOut() {
			if (this.hasLogin) {
				uni.showToast({
					icon: 'none',
					title: '注销成功',
				});
				this.logout();
				uni.navigateBack();
			} else {
				uni.showToast({
					icon: 'none',
					title: '您尚未登录哟~',
				});
			}
		},
		send() {
			if (this.phone.length != 11) {
				this.loading = false;
				uni.showToast({
					icon: 'none',
					title: '请检查手机号是否正确'
				});
				return;
			}
			if (!(/^1(3|4|5|7|8)\d{9}$/.test(this.phone))) {
				this.loading = false;
				uni.showToast({
					icon: 'none',
					title: '请检查手机号是否正确！'
				});
				return;
			}
		},
		switch1Change: function(e) {
			console.log('switch1 发生 change 事件，携带值为', e.target.value)
		},
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为：' + e.target.value)
			this.index = e.target.value
		}
	}
}
